  //function to read file
   readfile() {
    // You can change the url of file path here
    var url = "/assets/SINGLE.xlsx";
    var oReq = new XMLHttpRequest();
    oReq.open("GET", url, true);
    oReq.responseType = "arraybuffer";


oReq.onload = function(e) {
  
  var arraybuffer = oReq.response;

  /* convert data to binary string */
  var data = new Uint8Array(arraybuffer);
 
  var arr = new Array();

  for(var i = 0; i != data.length; ++i) arr[i] = String.fromCharCode(data[i]);
  var bstr = arr.join("");

  /* Call XLSX */
  var workbook = XLSX.read(bstr, {type:"binary"});

  /* DO SOMETHING WITH workbook HERE */
  var first_sheet_name = workbook.SheetNames[0];

  var second_sheet_name = workbook.SheetNames[1];

  /* Get worksheet */
  var worksheet1 = workbook.Sheets[first_sheet_name];
  console.log(XLSX.utils.sheet_to_json(worksheet1,{raw:true}));
  
  
  this.myExcelData = XLSX.utils.sheet_to_json(worksheet1,{raw:true})

   var worksheet2 = workbook.Sheets[second_sheet_name];
  
  console.log(XLSX.utils.sheet_to_json(worksheet2,{raw:true}));
  
  
  this.securitydataExcel = XLSX.utils.sheet_to_json(worksheet2,{raw:true})

}.bind(this.myExcelData)
console.log('send')
oReq.send();

}


